<template>
	<view class="content">
		<view class="header">
			<view class="header_text" @click="IOT_check_status()">
				Anchor1
			</view>
			<view class="header_status">
				{{status}}
			</view>
		</view>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				status: ''

			}
		},
		methods: {
			IOT_check_status() {
				let that = this;
				uniCloud.callFunction({
					name: 'IOT_GetDeviceStatus',
					success: (res) => {
						that.status = res.result;
						console.log(res);
					}
				})
			}
		},
		onShow() {
			setTimeout(setInterval(IOT_check_status(), 1000), 1500);

		},
		onLoad() {

		}

	}
</script>

<style>
	@import "../../common/index.css";
</style>
